/*
* 草稿
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Row, Col, Empty, Icon, Tag, Input, Button, AutoComplete, Spin, Checkbox, Menu, Dropdown, message, Affix, Modal} from 'antd';
import * as  mobx from 'mobx';
import moment from 'moment';
import {Scrollbars} from 'react-custom-scrollbars';
import InfiniteScroll from 'react-infinite-scroller';
import EditeMeeting from '@/components/digitalOrg/EditeMeeting';
import EditeVisit from '@/components/digitalOrg/EditeVisit';
import emitter from "@/utils/events";
import {detectZoom} from "@/utils/utils";

const confirm = Modal.confirm;

@inject('DigtalOrgStore')
@observer
export default class Draft extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DigtalOrgStore;
		this.state = {
			digtalTem: [], // 类型
			ratio: 100
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		let _this = this;
		this.store.getJournalTypeList();
		this.store.getJournalTagList();
		this.store.getUserPermissionByAppCode();
		// 获取大文件上传sts
		this.store.getSts();
		// 初始化查询
		_this.getTable();
		// 时间监听
		_this.DraftEmitter = emitter.addListener('draft', (draft) => {
			_this.getTable();
		});
		window.addEventListener("resize", function() {
			_this.setState({
				ratio: detectZoom()
			});}, false);
	}

	componentWillMount() {
		if (this.DraftEmitter) {
			emitter.removeListener(this.DraftEmitter);
		}
		window.removeEventListener("resize", function() {
			this.setState({
				ratio: 100
			});}, false);
	}

	// 切换详情
	handleEdit = (param) => {
		let dataArray = mobx.toJS(this.store.maintableData).map((item) => Object.assign(item, {selested: false}));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selested: true, isRead: true}))
		this.store.maintableData = Object.assign([], dataArray);
		this.store.draftId = param.id;
		this.store.temList = Object.assign({}, mobx.toJS(this.store.digtalTemData).filter(item=>item.type==param.type)[0]);
		this.store.getJournalById(param.id)
			.then((res) => {
				this.store.detailData = Object.assign({}, res);
			});
	}

	// 获取下一页信息
	handleInfiniteOnLoad = () => {
		let dataArray = mobx.toJS(this.store.maintableData);
		if (this.store.maintotalCount === dataArray.length) {
			return;
		}
		this.store.loading = true;
		this.store.mainQuery.pageIndex = this.store.mainQuery.pageIndex + 1;
		this.store.getJournalMainPageNew().then((res) => {
			const data = res;
			dataArray = dataArray.concat(data.body);
			this.store.maintableData = Object.assign([], dataArray);
			this.store.loading = false;
		});
	}

	// 前天、昨天、今天判断
	showText = (timeString, hm) => {
		let date = new Date(timeString)
		let today = new Date()
		today.setHours(0)
		today.setMinutes(0)
		today.setSeconds(0)
		// today 为今天凌晨的时间
		let dayTime = 24 * 60 * 60 * 1000
		let delta = today - date // 得到相差的时间 ms
		if (delta > 0) {
			if (delta <= dayTime) {
				return '昨天 ' + hm;
			} else if (delta <= 2 * dayTime) {
				return '前天 ' + hm;
			}
		} else if (-delta < dayTime) {
			return '今天 ' + hm;
		}
		return moment(timeString).format("MM-DD HH:mm");
	}

	onClose = () => {
		this.store.visible = false;
	}

	handleClick = (type) => {
		this.store.mainQuery.orderType = type;
		this.store.mainQuery.pageIndex = 1;
		this.store.mainQuery.pageSize = 10;
		this.store.getJournalMainPage().then((res) => {
			if (res) {
				if (mobx.toJS(this.store.maintableData).length > 0) {
					// 初始化默认值，列表第一项
					let param = mobx.toJS(this.store.maintableData)[0];
					this.selectClick(param);
				} else {
					return false;
				}
			}
		});
	}

	// 设置状态
	onChangeState = (type, item, e) => {
		let newData = JSON.parse(JSON.stringify(this.state[type]));
		let index = newData.findIndex(vitem => vitem == item.type);
		const {checked} = e.target;
		if (index == -1) {  // 新增
			this.setState({
				[type]: [...newData, item.type]
			}, () => {
				// 查询
				this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
					type: Object.assign([], this.state[type]),
					pageIndex: 1,
					pageSize: 10
				});
				this.store.getJournalMainPage();
			});
		} else {
			if (checked == false) { // 取消选中
				newData.splice(index, 1);
				this.setState({
					[type]: Object.assign([], newData)
				}, () => {
					// 查询
					this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
						type: Object.assign([], this.state[type]),
						pageIndex: 1,
						pageSize: 10
					});
					this.store.getJournalMainPage();
				});
			}
		}
	}

	onSelect = (value) => {
		// 查询
		this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
			text: value,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getJournalMainPage();
	}

	onChange = (value) => {
		// 查询
		this.store.mainQuery = Object.assign(mobx.toJS(this.store.mainQuery), {
			text: value,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getJournalMainPage();
	}

	onAsync = (type, id, index) => {
		this.store.syncAssignment(id).then((res) => {
			if (res) {
				message.success("同步成功");
				// 前端同步数据
				let detailData = JSON.parse(JSON.stringify(this.store.detailData));
				let assignments = detailData[type];
				assignments[index].isSync = 1; // 同步成功
				this.store.detailData = Object.assign(detailData, {
					[type]: assignments
				});
			}
		})
	}

	onRef = (ref) => {
		this.child = ref
	}

	// 重查
	getTable = () => {
		let _this = this;
		_this.store.mainQuery = Object.assign(mobx.toJS(_this.store.mainQuery), {
			saveType: 1,   // saveType = 1草稿箱 saveType=2是提交的日志
			text: "",
			tag: [],
			type: [],
			orderType: 0,
			pageIndex: 1,
			pageSize: 10,
			category: 1
		});
		_this.store.getJournalMainPage().then((res) => {
			if (res) {
				if (mobx.toJS(_this.store.maintableData).length > 0) {
					// 初始化默认值，列表第一项
					let param = mobx.toJS(_this.store.maintableData)[0];
					_this.selectClick(param);
				} else {
					return false;
				}
			}
		});
	}

	// 删除草稿
	showDeleteConfirm = async (id) => {
		let _this = this;
		confirm({
			title: '您确定删除吗?',
			okText: '确定',
			okType: 'danger',
			cancelText: '取消',
			onOk() {
				_this.store.delJournal(id)
				.then((res)=>{
					if (res) {
						message.success("删除成功");
                        _this.getTable();
					}
				});
			},
			onCancel() {
				console.log('Cancel');
			},
		});
	}

	updateState = (type, params) => {
	   this.setState({
		   [type]: params
	   });
	}

	// 选中点击
    selectClick = (param) => {
	    this.handleEdit(param);
    }

	// 提交发布成功，跳转到知识管理
	handleRoute = () => {
		this.store.digtalKey = "2";
		this.props.history.push("/digital/org");
		emitter.emit("journal", "2");
	}

	// 草稿保存成功，跳转到草稿箱
	handleDraftRoute = () => {
		this.store.digtalKey = 0;
		this.props.history.push("/digital/draft");
		emitter.emit("draft", "3");
	}

	hanndleVis = () => {
		const {mainQuery} = this.store;
		const orderType = mobx.toJS(mainQuery).orderType;
		if (orderType==0) {
			this.handleClick(1);
		} else if (orderType==1) {
			this.handleClick(2);
		} else {
			this.handleClick(1);
		}
	}

	hanndleVis2 = () => {
		const {mainQuery} = this.store;
		const orderType = mobx.toJS(mainQuery).orderType;
		if (orderType==0) {
			this.handleClick(4);
		} else if (orderType==4) {
			this.handleClick(3);
		} else {
			this.handleClick(4);
		}
	}

	render() {
		const {maintableData, loading, hasMore, maintotalCount, digtalTemData, mainQuery, tagList, detailData, hasJournal, temList} = this.store;
		const options = mobx.toJS(tagList).map(opt => opt.name);
		const menu = (
			<Menu>
				{mobx.toJS(digtalTemData).map((item, index) => {
					return <Menu.Item key={item.type}>
						<Checkbox
							checked={this.state["digtalTem"].includes(item.type)}
							key={item.type + "-" + index}
							onChange={this.onChangeState.bind(this, "digtalTem", item)}
						>{item.name}</Checkbox></Menu.Item>
				})}
			</Menu>
		);
		const orderType = mobx.toJS(mainQuery).orderType;
		const temType = mobx.toJS(temList).type;
		const {ratio} =this.state;


		return (
			<Row className="w clearfix">
				<div className="fl" style={{paddingRight: '16px', width: '330px'}}>
					<div className="clearfix" style={{
						display: 'flex',
						alignItems:'center',
						justifyContent: 'flext-start',
						padding: '0 0 10px 0',
						borderBottom: '1px solid #e6e6e6'}}>
						<Button size="small" type="link" style={{padding: '0 2px'}} onClick={this.hanndleVis}>最新</Button>
						<span style={{display: 'inline-block'}}>
							<Icon onClick={this.handleClick.bind(this, 2)} type="caret-up" className={orderType==1?"caret-up digital_icon_active":"digital_icon caret-up"}/>
	                        <Icon onClick={this.handleClick.bind(this, 1)} type="caret-down" className={orderType==2?"caret-down digital_icon_active":"digital_icon caret-down"}/>
						</span>
						<Button size="small" type="link" style={{marginLeft:'1px', padding: '0 2px'}} onClick={this.hanndleVis2}>最热</Button>
						<span style={{display: 'inline-block'}}>
							<Icon onClick={this.handleClick.bind(this, 3)} type="caret-up" className={orderType==4?"caret-up digital_icon_active":"digital_icon caret-up"}/>
	                        <Icon onClick={this.handleClick.bind(this, 4)} type="caret-down" className={orderType==3?"caret-down digital_icon_active":"digital_icon caret-down"}/>
						</span>
						<Dropdown overlay={menu} trigger={['click']}>
							<Button size="small" className="ml5">类型 <Icon style={{fontSize: '12px'}} type="filter"/></Button>
						</Dropdown>
						<AutoComplete
							className="certain-category-search fr"
							dropdownClassName="certain-category-search-dropdown"
							dropdownMatchSelectWidth={false}
							dropdownStyle={{width: 125}}
							size="middle"
							style={{width: '145px', marginLeft: '6px'}}
							dataSource={options}
							placeholder="请输入"
							optionLabelProp="value"
							onSelect={this.onSelect}
							onSearch={this.onChange}
						>
							<Input suffix={<Icon type="search" className="certain-category-icon"/>}/>
						</AutoComplete>
					</div>
					<Affix offsetTop={60}>
					<div className="require-table">
						<Scrollbars
							style={{
								minHeight: "500px",
								height: "calc(100vh - "+ ratio<100?(300+3*ratio):(300)+"px)"
							}}
							autoHide={false}>
							{mobx.toJS(maintableData).length > 0 ?
								<InfiniteScroll
									initialLoad={false}
									pageStart={0}
									loadMore={this.handleInfiniteOnLoad.bind(this)}
									hasMore={!loading && hasMore}
									useWindow={false}
								>
									{mobx.toJS(maintableData).length > 0 && mobx.toJS(maintableData).map((item, index) => {
										return (<div
											className={item.selested == true ? "require-tr require-tr-active" : "require-tr"}
											onClick={this.selectClick.bind(this, item)}
											key={'tab-' + index}>
											<span className={item.isRead == true ? "require-td" : "require-td active"} title={item.title}>{item.title}</span>
											<span className="require-title">
												<span className="mr5">{item.userName}</span>
												{item.tags == null ? null : item.tags.map((item, index) => {
													return <Tag
														className={index >= 2 ? "hidden" : ""}
														color="gold">{item.name}</Tag>
												})}
											</span>
											<span className="require-lavel">{moment(item.actualTime).format("YYYY-MM-DD")}</span>
											<Icon type="close" onClick={this.showDeleteConfirm.bind(this, item.id)} title={'删除'}
											      className={"require-del"}
											      style={{position: 'absolute', right: '10px', top: '10px', color: 'rgb(217, 0, 27'}}
											/>
										</div>)
									})}
									{loading && hasMore && (
										<div className="demo-loading-container">
											<Spin/>
										</div>
									)}
								</InfiniteScroll> : <Empty imageStyle={{marginTop: "50px"}}/>}
						</Scrollbars>
					</div>
						<div style={{
							fontSize: '12px',
							color: 'rgb(153, 153, 153)'
						}}>{`共 ${maintotalCount} 条 `}</div>
					</Affix>
				</div>
				<div className="fl" style={{width: 'calc(100% - 350px)', borderLeft: '1px solid #EBEBEB', minHeight: '350px', paddingLeft: '15px'}}>
					{temType==="meeting"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> : <EditeMeeting
							onRef={this.onRef}
							store={this.store}
							detailData={mobx.toJS(detailData)}
							pathname={this.props.location.pathname}
							handleRoute={this.handleRoute}
							handleDraftRoute={this.handleDraftRoute}
							getTable={this.getTable}
						/>:null}
					{temType==="visit"||temType=="monthlySharing"||temType=="summary"||temType=="replay"||temType==="news"?mobx.toJS(maintableData).length == 0 ? <Empty imageStyle={{marginTop: "50px"}}/> : <EditeVisit
						onRef={this.onRef}
						store={this.store}
						detailData={mobx.toJS(detailData)}
						pathname={this.props.location.pathname}
						handleRoute={this.handleRoute}
						getTable={this.getTable}
						temType={temType}
					/>:null}
				</div>
			</Row>
		);
	}
}





